/* EditingTemplate */


.template {

  padding: 40px;
  top: 50%;
  left: 50%;
  position: fixed;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 6;
  width: 600px;
  justify-items: center;
}

.boxTitleBtn {
  width: 580px;
  display: flex;

}

.saveSubmitBtn {
  width: 385px;
  height: 40px;
  border-radius: 50px;
  border: none;
  background: #fdad6d;
  color: #5e2d07;
}

.SubmitBtn {
  margin-right: 20px;
  border-radius: 50px;
  border: 2px solid #84a6c2;
  background: transparent;
  color: #84a6c2;
  width: 220px;
  height: 40px;
}

.buttonClose {
  margin-left: auto;
  background: transparent;
  border: none;
}

.userList {
  margin-bottom: 40px;
}

.userItemtext {
  margin-right: 40px;
  color: rgb(67, 96, 124);
  font-family: Verdana;
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
}

.userListInput {
  padding: 10px 20px 10px 20px;
  width: 360px;
  height: 40px;
  box-sizing: border-box;
  /* Border */
  border: 1px solid rgb(223, 223, 223);
  border-radius: 50px;
}

.userListItem {
  display: flex;
}


.makeListItem {
  /* margin-left: 40px; */
  align-items: center;
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
}

.makeList {
  /* ; */
  /* justify-items: center; */
  border-top: 1px solid rgb(223, 223, 223);
  border-bottom: 1px solid rgb(223, 223, 223);
}


.makeItemText {
  margin: 0;

  color: rgb(1, 25, 54);
  font-family: Verdana;
  font-size: 16px;
  font-weight: 400;
  line-height: 120%;
  letter-spacing: 0%;
}

.makeItemEl {
  margin-left: 130px;
  margin-right: 15px;
  padding: 10px 20px 10px 20px;
  text-align: left;
  background: transparent;
  width: 340px;
  height: 40px;
  box-sizing: border-box;
  /* Border */
  border: 1px solid rgb(223, 223, 223);
  border-radius: 50px;
}

.makeItemElBtn {
  /* margin-left: 130px;  */
  padding: 10px 20px 10px 20px;
  text-align: left;
  background: transparent;
  width: 340px;
  height: 64px;
  box-sizing: border-box;
  /* Border */
  border: 1px solid rgb(223, 223, 223);
  border-radius: 50px;
}

.makeItemAddText {
  margin-top: 5px;
  color: rgb(132, 166, 194);
  font-family: Verdana;
  font-size: 14px;
  font-weight: 400;
  line-height: 120%;
}

.componentList {
  margin-left: 80px;
}

.componentListItem {
  margin-top: 5px;
}

.commentBox {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
}

.comment {
  padding: 10px 20px 10px 20px;
  margin-left: 30px;
  width: 370px;
  height: 116px;
  box-sizing: border-box;
  /* Border */
  border: 1px solid rgb(223, 223, 223);
  border-radius: 20px;
  color: rgb(67, 96, 124);
  font-family: Verdana;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.commentText {
  margin: 0;
  color: rgb(1, 25, 54);
  font-family: Verdana;
  font-size: 16px;
  font-weight: 400;
  line-height: 120%;
  /* margin-left: 10px; */
}

.infoComment {
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
}

.commentInput {
  margin-right: 7px;
  width: 25px;
  height: 25px;
  border: 2px solid rgb(67, 96, 124);
}